  .form__label {
    margin-bottom: 12px;
    font-size: 16px;
  }
}
@media (orientation: portrait) {
  .form__label {
    opacity: 1;
    pointer-events: auto;
  }
}

.form__field {
  position: absolute;
  right: 21rem;
  flex: none;
  padding: 0;
  height: 6rem;
  width: 0;
  text-transform: uppercase;
  font: inherit;
  font-size: 2.4rem;
  line-height: 1.2;
  color: #5f458c;
  border: 0;
  border-radius: 3rem;
  outline: none;
  transition: width 0.75s ease, padding 0.75s ease;
}
.form__field:focus {
  border: 1px solid #a67ee5;
}
